<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="10%">学科</th>
          <th width="10%">教师姓名</th>
          <th width="10%">论文</th>
          <th width="12%">赛课及指导学生</th>
          <th width="10%">课题、课程</th>
          <th width="15%">经验交流及学术报告</th>
          <th width="8%">总分</th>
          <th width="25%">备注</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.teachGroupName}}</td>
          <td>{{item.teacherName}}</td>
          <td>{{item.scorePaper || '--'}}</td>
          <td>{{item.scoreCompetition || '--'}}</td>
          <td>{{item.scoreTopics || '--'}}</td>
          <td>{{item.scoreAcademic || '--'}}</td>
          <td>{{item.scoreTotal || '--'}}</td>
          <td>{{item.remark || '--'}}</td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "教研成果", path: ""}],
        searchData: [
          {type: 'select',title: '学期', width: '240px', notRemove: true, model: 'termId', option: []},
          {type: 'select',title: '学科', notRemove: true, model: 'courseId', option: []},
        ],
        table:{
          url: '/teachingResearch/queryTeachingResearchPageWithAccess.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            termId: null,
            courseId: null
          }
        },
        dialog: false,
        form: {
          teachGroupId: null,
          courseName: ''
        },
      }
    },
    methods: {
      // 获取学期
      async getTermList(){
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.searchData[0].option = res.map(e=> {
          return {label: e.termName, value: e.schoolTermId}
        })
        res.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.table.query.termId = e.schoolTermId
          }
        })
      },
      // 获取学科
      async subject(){
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do'
        })
        this.searchData[1].option = res.map(e=>{
          return {value: e.id,label: e.name}
        })
        this.table.query.courseId = res[0].id
      },
    },
    async mounted(){
      await this.getTermList()
      await this.subject()
      this.$search(this.table)

    }
  }
</script>
<style lang="sass" scoped>
  .dialog
    .title
      width: 70px
      line-height: 40px
      vertical-align: top
      display: inline-block
    .input
      width: calc(100% - 70px)
    .imp:before
      content: '*'
      color: $error-c
      margin-right: 4px
    .file-input
      /deep/ input
        padding-top: 10px
        line-height: 1
    .item
      margin-bottom: 20px
    .btns
      text-align: center
</style>
